body, html {
  font-size: 0.9rem;
  background: #f2f3f7
}
body, .form-control {
  font-size: 0.9rem
}
body,
html,
#root,
.App {
  min-height: 100vh
}
.btn {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 0.66rem;
  border-radius: 0.5rem;
  box-shadow: 0 .1rem .1rem rgba(0,0,0,.05)!important
}
.App-link {
  color: #61dafb;
}

.btn-light {
  border-color: #dedede
}

.btn-light:hover {
  border-color: #dedede;
  background-color: #f3f3f3
}

.shadow {
  box-shadow: 0 .1rem .1rem rgba(0,0,0,.05)!important
}

.modal-content,
.card {
  overflow: hidden;
  border-radius: 0.756rem
}

hr {
  opacity: 0.1
}

.table th {
  border-top: 1px solid #0000001a;
  border-bottom: 1px solid #0000001a;
  border-bottom-color: #0000001a!important
}
.table td {
   text-align: left;
}

.form-control {
  background: #f5f7f9;
  font-weight: 400;
  padding: .5rem .75rem;
  border: none
}
.form-control:hover {
  background: #f5f7f9;
}
.form-control::placeholder,
.form-control::-ms-input-placeholder,
.form-control:-ms-input-placeholder {
  color: #9fa8b0
}
.form-control:focus {
  background: #f5f7f9;
}

#invoiceCapture {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.dropdown-toggle {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem; 
  line-height: 1.5;
  border-radius: 0.25rem; 
}
a{
  text-decoration: none;
}
.widgit{
  width: 10%;
  border: 1px solid gray;
  padding: 10px;
  background-color: black;
  color: white;
}
.container{
  padding: 10px;
  margin-top: 10px;
}
.pagination-button{
  margin-right: 5px;
  padding: 3px 15px;
}

.h1, h1 {
  font-size: calc(1.375rem + 1.5vw);
  padding-left: 500px;
}
.btn-primary {
  color: #fff;
  background-color: black;
  border-color: #11499b;
}

.btn-primary :hover{
  background-color: #dedede;
}

.btn-danger {
  color: #fff;
  background-color: #000000;
  border-color: #090808;
}

.btn-danger :hover{
  background-color: #dedede;
}
.invoice-editor .logo .placeholder {
  background: #f5f5f5;
  border: 1px solid var(--papers-gray-1);
  border-radius: 4px;
  height: 130px;
  padding: 50px 0 0;
  text-align: center;
  width: 100%;
}